import React from "react";
import { Progress } from "antd";
const Analytics = ({allTransaction}) => {
    const categories =['Salary','Tip','Project','Food','Movie','Bills','Medical','Fee','Tax',];


    //total transactions
    const totalTransaction = allTransaction.length;
    const totalIncomeTransactions = allTransaction.filter(
        ( Transaction ) => Transaction.type === 'Income');
    const totalExpenseTransactions = allTransaction.filter((Transaction) => Transaction.type === 'Expense');
    const totalIncomePercent = (totalIncomeTransactions.length/totalTransaction) * 100;
    const totalExpensePercent =(totalExpenseTransactions.length/totalTransaction) * 100;

    //total turnovers
    const totalTurnover = allTransaction.reduce((acc, Transaction) => acc + Transaction.amount,0);
    const totalIncomeTurnover = allTransaction.filter( Transaction => Transaction.type === 'Income'
    ).reduce((acc, Transaction) => acc + Transaction.amount,0);
    const totalExpenseTurnover = allTransaction.filter( Transaction => Transaction.type === 'Expense'
    ).reduce((acc, Transaction) => acc + Transaction.amount,0);
    const totalIncomeTurnoverPercent = (totalIncomeTurnover/totalTurnover) *100;
    const totalExpenseTurnoverPercent = (totalExpenseTurnover/totalTurnover) *100;



  return (
    <>
    <div className="row m-3">
        <div className="col-md-4">
            <div className="card">
                <div className="card-header">
                    Total Transactions : {totalTransaction}
                </div>
                <div className="card-body">
                    <h5 className="text-success">Income : { totalIncomeTransactions.length}</h5>
                    <h5 className="text-danger"> Expense : { totalExpenseTransactions.length}</h5>
                    <div>
                        <Progress type="circle" strokeColor={'green'} className="mx-2"
                        percent={totalIncomePercent.toFixed(0)}/>
                        <Progress type="circle" strokeColor={'red'} className="mx-2"
                        percent={totalExpensePercent.toFixed(0)}/>
                    </div>
                </div>
            </div>
        </div>
        <div className="col-md-4">
        <div className="card">
                <div className="card-header">
                    Total Turnover : {totalTurnover}
                </div>
                <div className="card-body">
                    <h5 className="text-success">Income : { totalIncomeTurnover}</h5>
                    <h5 className="text-danger"> Expense : { totalExpenseTurnover}</h5>
                    <div>
                        <Progress type="circle" strokeColor={'green'} className="mx-2"
                        percent={totalIncomeTurnoverPercent.toFixed(0)}/>
                        <Progress type="circle" strokeColor={'red'} className="mx-2"
                        percent={totalExpenseTurnoverPercent.toFixed(0)}/>
                    </div>
                </div>
            </div>
        </div>

    </div>
    <div className="row mt-3">
        <div className="col-md-4">
            <h4>Categorywise Income</h4>
            {
                categories.map((category) =>{
                    const amount = allTransaction.filter((Transaction)=> Transaction.type === "Income" && 
                    Transaction.category === category
                    ).reduce((acc, Transaction) => acc + Transaction.amount, 0);
                    return (
                        amount > 0 &&(
                        <div className="card">
                            <div className="card-body">
                                <h5>{category}</h5>
                                <Progress percent={((amount / totalIncomeTurnover) * 100).toFixed(0)} />
                            </div>
                        </div>
                        )
                    );
                })
            }
            </div>
             <div className="col-md-4">
            <h4>Categorywise Expense</h4>
            {
                categories.map((category) =>{
                    const amount = allTransaction.filter((Transaction)=> Transaction.type === "Expense" && 
                    Transaction.category === category
                    ).reduce((acc, Transaction) => acc + Transaction.amount, 0);
                    return (
                        amount > 0 &&(
                        <div className="card">
                            <div className="card-body">
                                <h5>{category}</h5>
                                <Progress percent={((amount / totalExpenseTurnover) * 100).toFixed(0)} />
                            </div>
                        </div>
                        )
                    );
                })
            }
        </div>
    </div>
    
    </>
  )
}

export default Analytics;