import React,{useState,useEffect} from 'react';
import {Form, Input, Modal, Select, Table, message,DatePicker} from "antd";
import {UnorderedListOutlined,AreaChartOutlined,EditOutlined,DeleteOutlined } from '@ant-design/icons';
import Layout from '../components/Layouts/Layout';
import axios from "axios";
import Spinner from '../components/Spinner';
import moment from "moment";
import Analytics from '../components/Analytics';
const { RangePicker } = DatePicker;


const HomePage = () => {
  const [showModal,setShowModal] =useState(false);
  const [loading,setLoading] = useState(false);
  const [allTransaction , setAllTransaction] =useState([]);
  const [frequency,setFrequency] = useState("7");
  const [selectedDate,setSelectedDate] = useState([]);
  const [type,setType] = useState("all")
  const [viewData,setViewData] = useState("table");
  const [editTable,setEditTable] = useState(null);

  //table data
  const columns =[
  {
  title: "date",
  dataIndex: "date",
  render : (text) => <span>{moment(text).format("YYYY-MM-DD")}</span>,
  },
  {
  title: "Amount",
  dataIndex: "amount",
  },
  {
  title: "Type",
  dataIndex: "type",
  },
  {
  title: "Category",
  dataIndex: "category",
  },
  {
  title: "Refrence",
  dataIndex: "refrence",
  },
  {
  title:"Actions",
  render :(text,record) => (
    <div>
      <EditOutlined onClick={() =>{
        setEditTable(record);
        setShowModal(true);
      }
      }/>
      <DeleteOutlined className="mx-2" onClick={() => {handleDelete(record)}}/>
    </div>
  )
  
  },
  ];

  //getall trasactions
  

useEffect(()=> {

  const getAllTransactions = async () => {
    try {
      const user = JSON.parse(localStorage.getItem("user"));
      setLoading(true);
      const res= await axios.post("/api/v1/Transactions/get-transaction",{userid: user.user._id,frequency,selectedDate,type});
      setLoading(false);
      setAllTransaction(res.data);
      console.log(res.data);

    } catch (error) {
      console.log(error);
      message.error("fetch issue with transaction");
      
    }
  };



  getAllTransactions();
},[frequency, selectedDate, type ]);

const handleDelete = async (record) => {
  try {
    setLoading(true);
    await axios.post ("/api/v1/Transactions/delete-transaction",{transactionsId:record._id})
    setLoading(false);
    message.success("Transactions deleted successfully")
  } catch (error) {
    setLoading(false);
    console.log (error);
    message.error("unable to delete transaction")
  }

}




  const handleSubmit = async (values) => {
    try {
      const user = JSON.parse(localStorage.getItem("user"));
      values.userid =user._id;
      setLoading(true);
      if (editTable){
        await axios.post("/api/v1/Transactions/edit-Transaction",{
          payload:{
            ...values,userId:user.user._id
          },
          transactionsId: editTable._id
        }
        );
        setLoading(false);
        message.success("Transaction Updated Success")
      }else{
     await axios.post("/api/v1/Transactions/add-transaction", {...values, userid: user.user._id,});
      setLoading(false);
      message.success("Successfully added transaction")
      }
      setShowModal(false);
      setEditTable(null);
    } catch (error) {
      setLoading(false);
      message.error("failed to add transaction");
    }
    
  };
  return (
    <Layout>
      {loading && <Spinner/>}
       <div className="filters">

        <div><h6>Select Frequency</h6>
        <Select value={frequency} onChange={(values) => setFrequency(values)}>
          <Select.Option value="7">Last 1 week</Select.Option>
          <Select.Option value="30">Last 1 Month</Select.Option>
          <Select.Option value="365">Last 1 Year</Select.Option>
          <Select.Option value="custom">custom</Select.Option>
        </Select>
        {frequency === "custom" && (
          <RangePicker value={selectedDate} onChange={(values) => setSelectedDate(values)}/>
        )}
        </div>
        <div><h6>Select Type</h6>
        <Select value={type} onChange={(values) => setType(values)}>
          <Select.Option value="All">All</Select.Option>
          <Select.Option value="Income">Income</Select.Option>
          <Select.Option value="Expense">Expense</Select.Option>
          
        </Select>
        {frequency === "custom" && (
          <RangePicker value={selectedDate} onChange={(values) => setSelectedDate(values)}/>
        )}
        </div>
        <div className="switch-icons">
            <UnorderedListOutlined className={`mx-2 ${viewData === 'table' ? 'active-icon': 'inactive-icon'}`}
             onClick={() => setViewData("table")}/>
            <AreaChartOutlined 
            className={`mx-2 ${viewData === 'analytics' ? 'active-icon': 'inactive-icon'}`} 
            onClick={()=> setViewData("analytics")}/>
          </div>
        <div>
        
          <button className="btn btn-primary" onClick={()=> setShowModal(true)}>Add New</button></div>
       </div>
       <div className="content">
        {viewData === 'table' ? (<Table columns={columns} dataSource={allTransaction}/>) :
        (<Analytics allTransaction={ allTransaction}/>)  }
        
       </div>
       <Modal title={ editTable ? "Edit Transaction" : "Add Transaction" }
       open={showModal}
       onCancel={()=> setShowModal(false)}
       footer={false}>
        <Form layout="vertical" onFinish={handleSubmit} initialValues={editTable}>
          <Form.Item label="Amount" name="amount">
            <Input type="text" />
          </Form.Item>
          <Form.Item label="Type" name="type">
            <Select>
              <Select.Option value="Income">Income</Select.Option>
              <Select.Option value="Expense">Expense</Select.Option>
            </Select>
          </Form.Item>
          <Form.Item label="Category" name="category">
            <Select>
              <Select.Option value="Salary">Salary</Select.Option>
              <Select.Option value="Tip">Tip</Select.Option>
              <Select.Option value="Project">Project</Select.Option>
              <Select.Option value="Food">Food</Select.Option>
              <Select.Option value="Movie">Movie</Select.Option>
              <Select.Option value="Bills">Bills</Select.Option>
              <Select.Option value="Medical">Medical</Select.Option>
              <Select.Option value="Fee">Fee</Select.Option>
              <Select.Option value="Tax">Tax</Select.Option>
            </Select>
          </Form.Item>
          <Form.Item label="Date" name="date">
            <Input type="date"/>
          </Form.Item>
          
          <Form.Item label="Refrence" name="refrence">
            <Input type="text"/>
          </Form.Item>
          <Form.Item label="Description" name="description">
            <Input type="text"/>
          </Form.Item>
          <div className="d-flex justify-content-end">
            <button type="submit" className="btn btn-primary">
              {" "}
              SAVE</button>
          </div>

        </Form>
       </Modal>
    </Layout>
  );
};

export default HomePage;